import React, { useState, useEffect } from 'react'
import './style.css'
import { useLanguageStore } from '../../../../../store/LanguageStore';
import { useColourObjectStore } from '../../../../../store/ColourStore';
import { useActivePopup } from '../../../../../store/ActivePopup';
import { userActiveLevelDetailsStore } from '../../../../../store/ActiveLevelDetailsStore';
import { userActiveSubLevelDetailsStore } from '../../../../../store/ActiveSubLevelDetailsStore';
import { useAuth } from '../../../../../store/authStore';
import { sendGamedata } from 'components/Common/core';
import useWindowDimensions from 'components/Common/useWindowDimensions';

const DidyouknowComponent = (props: any) => {
  const { colourObj } = useColourObjectStore();
  const { Language, setLanguage } = useLanguageStore();
  const { ActivePopup, setActivePopup } = useActivePopup();
  const { ActiveLevelDetails, loadActiveleveldetails, levelLogError } = userActiveLevelDetailsStore();
  const { ActiveSubLevelDetails, loadActiveSubleveldetails, sublevelLogError } = userActiveSubLevelDetailsStore();
  const { auth } = useAuth();
  const { height, width } = useWindowDimensions()
  const [count, setCount] = useState(0)

  const sublevelSubmit = async (data: any) => {
    setActivePopup("loader")
    await sendGamedata(props?.id, "SUBLEVEL", data, "", "DONE", auth)

    props.subleveldata.map(async (object: any, id: any) => {
      if (object.id === props.id) {
        if (props.subleveldata[id + 1]?.id !== undefined) {
          await loadActiveSubleveldetails(props.subleveldata[id + 1]?.id)
          localStorage.setItem("ActiveSubLevel", props.subleveldata[id + 1]?.id)
        } else {
          await sendGamedata(ActiveLevelDetails?.id, "LEVEL", "Level Successfully Completed", "", "DONE", auth)
          loadActiveleveldetails(ActiveLevelDetails?.id)
        }

        if (levelLogError?.status !== 200 || sublevelLogError?.status !== 200 || levelLogError?.status !== 201 || sublevelLogError?.status !== 201) {
          setActivePopup("submiterror")
          setCount(count + 1)

          if (count >= 3) {
            setActivePopup("techissue")
          }
        }
        setActivePopup("submit")
      }
    })
  }

  return (
    <div className={width <= 375 ? "sublevel_wrapper padding-helper" : 'sublevel_wrapper'} id='activity_input_value'>
      <div className="sublevel_subtitle_container">
        <div className='Title_OrangeU' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>
          {(Language?.value === "KANNADA") ? props.TextData[0]?.title : props.sl_title}</div>
        {props.sl_stitle !== undefined && <div className='sublevel_subtitle'>{(Language?.value === "KANNADA") ? props.TextData[0]?.s_title : props.sl_stitle}</div>}
      </div>

      <div className="sublevel_body_container">
        {props.sl_icon !== undefined && <div className='sublevel_icon'>
          <img className='sublevel_logo' src={props?.sl_icon} />
        </div>}
        {props.sl_body !== undefined && <div className='sublevel_bodytext' style={{ color: colourObj.color_m1 }}>
          {(Language?.value === "KANNADA") ? props.TextData[0]?.body : props.sl_body}</div>}

        <div className="button_container">
          <button
            className="continue_button_Green"
            style={{ backgroundColor: colourObj.color_green, marginTop: "-5%" }}
            onClick={() => {
              sublevelSubmit("Information has been read")
            }}>
            <div className="button_text" style={{ color: colourObj.color_t1 }}>{(Language?.value === "KANNADA") ? "ಮುಗಿದಿದೆ" : "DONE"}</div>
          </button>
        </div>
      </div>
    </div>
  )
}

export default DidyouknowComponent;
